$header-height: 3rem;

.app-header {
    display: flex;
    justify-content: space-between;
}

.logo-wrapper {
	width: 15rem;

    .app-logo {
		animation: App-logo-spin infinite 20s linear;
        height: $header-height;
    }

    .app-title {
        font-size: 1.5rem;
        line-height: $header-height;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
